.header__nav-link {
  color: #A9A9A9;
  margin: 0;
  text-decoration: none;
  font-weight: 400;
  margin-left: 24px;
  height: 22px;
  transition: opacity .4s linear;
}

.header__nav-link:hover {
  opacity: .6;
}

@media screen and (max-width: 700px) {
  .header__nav-link {
    margin: 0;
  }
}
